
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
name:'edit',
props:['id'],
components:{
XTextarea,Group,mavonEditor,XButton,Toast
},
data:function () {
return {
topic:{
title:'',
content:'',
},
toolbars:{
bold: true, // 
header: true, // 
mark: true, // 
quote: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
trash: true, // 
save: false, // eventssave
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true 

}
}
},
beforeRouteEnter:function (to,from,next) {
next(function(vm) {
vm.$http.get('/topic/'+to.params.id).then(function (res) {
console.log(res.data.data)
vm.topic=res.data.data
})
})
},
methods:{

edittopic(){
var app=this;
this.$http.post('/topics/update',{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id,
title:this.topic.title,
tab:'dev',
content:this.topic.content
}).then(function (res) {
console.log(res)
app.$router.go(-1)
})
}
}
}



