
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mapMutations} from 'vuex'
export default {
components:{
XInput,Group,XButton,Toast
},
data:function(){
return {
accesstoken:'0892fdda-6ed0-4740-9d78-6f002c641d7b',
isShowToast:false ,//
toastText:''
}
},
methods:{
...mapMutations(['mutationIsLogined','mutationLoginName','mutationauthorId','mutationaccesstoken']),
dologin(){
var that =this
this.$http.post('/accesstoken',{
accesstoken:this.accesstoken
}).then(function(res){

if(res.data.success){

that.mutationIsLogined()


that.$router.push('/')

window.localStorage.setItem('loginStatus',true)
window.localStorage.setItem('loginname',res.data.loginname)
window.localStorage.setItem('accesstoken',that.accesstoken)
window.localStorage.setItem('author_id',res.data.id)
that.mutationLoginName(res.data.loginname)
that.mutationauthorId(res.data.id)
that.mutationaccesstoken(that.accesstoken)
}
}).catch(function(error){

that.isShowToast=true
that.toastText="token"
})
}
}
}



