
import Msg from 'vux/src/components/msg/index.vue'


export default {
name:'logout',
created:function () {
if(this.$store.state.isLogined==false){
return;
}

this.$store.commit('mutataionLogout')

window.localStorage.clear()

this.$router.push('/')

},
components:{
Msg
},
data:function () {
return{
buttons: [{
type: 'primary',
text: '',
link:'/login'
}]
}

}
}



