
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import {mapState} from 'vuex'
export default {
name:'topic',
props:['id'],
directives: {
TransferDom
},
components:{
CellBox,
Group,
Flexbox,
FlexboxItem,
GroupTitle,
XButton,
Popup,
XTextarea
},
data:function(){
return{
topic:null,
isShowPopup:false, //
replyContent:'',
reply_id:null,
reply_loginname:null
}
},
computed:{
...mapState(['isLogined'])
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.getTopic()
})
},
methods:{


addreply(replyTab){
var app=this;

var params={
accesstoken:this.$store.state.accesstoken,
content:this.replyContent}
if(replyTab==2){
params.reply_id=this.reply_id,
params.content='@'+this.reply_loginname+' '+params.content
}
this.$http.post('/topic/'+this.topic.id+'/replies',params).then(function(res) {

if(res.data.success){

app.getTopic()
}
})

app.replyContent='',
app.isShowPopup=false
app.reply_id='',
reply_loginname=''

},

showPopup(replyId,replyLoginname){
this.isShowPopup=true
this.reply_id=replyId
this.reply_loginname=replyLoginname
},

dianji(uid){
var app=this
this.$http.post('/reply/'+uid+'/ups',{
accesstoken:this.$store.state.accesstoken,
}).then(function (res) {
console.log(res.data)
var type=res.data.action  
if(type=='up'){


var reply;
for(var i=0;i<app.topic.replies.length;i++){
if(uid==app.topic.replies[i].id){
reply=app.topic.replies[i]
break
}
}

reply.ups.push(app.$store.state.accesstoken)
}else if(type=='down'){

var reply;
for(var i=0;i<app.topic.replies.length;i++){
if(uid==app.topic.replies[i].id){
reply=app.topic.replies[i]
break
}
}

for(var j=0;j<reply.ups.length;j++){
if(reply.ups[j]==app.$store.state.accesstoken){
reply.ups.splice(j,1)
break
}
}

}
})
},

collect(){
var app=this;
this.$http.post('/topic_collect/collect',{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id
}).then(function(res){

app.topic.is_collect=true

})
},

decollect(){
var app=this;
this.$http.post('/topic_collect/de_collect',{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id
}).then(function(res){
app.topic.is_collect=false

})
},
getTopic(){
var app=this
this.$http.get('/topic/'+this.id).then(function(res){
console.log(res.data.data)
app.topic=res.data.data
})
}
}
}



