import Vue from 'vue'
import Router from 'vue-router'
import TopicList from '../components/TopicList'
import Topic from '../components/Topic.vue'
import Login from '../components/Login.vue'
import User from '../components/User.vue'
import xiangqin from '../components/xiangqin.vue'
import Center from '../components/Center'
import Logout from '../components/Logout.vue'
import Create from '../components/Create.vue'
import Edit from '../components/Edit.vue'
import Msg from '../components/Msg.vue'
Vue.use(Router)
//
const originalPush = Router.prototype.push
   Router.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}

export default new Router({
  routes: [
    { path: '/',  redirect:'/topiclist/all'   },
    { path:'/topiclist/:name',component:TopicList,props:true},   //props     
    { path:'/topic/:id',component:Topic ,props:true},
    {path:'/login/',component:Login ,props:true },
    {path:'/user/:id',component:User ,props:true },
    {path:'/center',component:Center ,props:true },
    {path:'/logout',component:Logout ,props:true },
    {path:'/create',component:Create ,props:true },
    {path:'/msg',component:Msg ,props:true },
    {path:'/topic/:id/edit',component:Edit ,props:true },
    {path:'/xiangqing/:loginname',component:xiangqin ,props:true },
   

  ]
})



