import Vue from 'vue';
import Vuex from 'vuex'

Vue.use(Vuex)


const store =new Vuex.Store({
    state:{
        topics:[
            {name:'all',desc:''},
            {name:'good',desc:''},
            {name:'share',desc:''},
            {name:'ask',desc:''},
            {name:'job',desc:''},
            {name:'dev',desc:''}
        ],
        isShowMoreMenus:false , //actionsheet
        //
        isLogined:false, //
        unLoginedMenus:{
            login:''
        },
        loginedMenus:{
            center:'',
            msg:'',
            create:'',
            logout:''
        },
        loginname:'' ,//
        accesstoken:'', //token
        authorId:'' //id
    },
    getters:{
        //
        moreMenus(state){
            if(state.isLogined){
                return state.loginedMenus
            }
            return state.unLoginedMenus
        }
    },
    mutations:{
        //
        mutationIsShowMoreMenus:function(state,playload){
            state.isShowMoreMenus=playload
        },
        //
        mutationIsLogined(state){
            state.isLogined=true
        },
        // 
        mutationLoginName( state,p){
            state.loginname=p
        },
        // token
        mutationaccesstoken( state,p){
            state.accesstoken=p
        },
        // id
        mutationauthorId( state,p){
            state.authorId=p
        },
        // {
            mutataionLogout(state){
                state.isLogined=false;
                state.loginname='';
                state.accesstoken=''
                state.authorId=''
            }
        

    },
    actions:{},
})
export default store


