
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {mapState,mapGetters,mapMutations} from 'vuex'
export default {
props:['name'],
data:function(){
return {
lists:[],

pullupconfig:{
content:'',
downContent:'',
upContent:'',
loadingContent:'...'
},
pulldownconfig:{
content:'',
downContent:'',
upContent:'',
loadingContent:'...'
},
page:1,//
limit:6,//
}
},
computed:{
...mapState(['topics','isShowMoreMenus']),
},
components:{
Cell,
Group,
Scroller,
Tab,
TabItem
},
methods:{

routeTo(index){

this.$router.push('/topiclist/'+this.topics[index].name)
},
loadMoreDate(){

this.page=this.page+1;
var app=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){

app.lists=app.lists.concat(res.data.data)


app.$refs.pull.donePullup(); 
})
},
upDate(){
var that=this
this.page=1
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){

that.lists=res.data.data
that.$refs.pull.donePulldown(); 
})
}
},

beforeRouteEnter:function(to,from,next){

next(function(vm){
vm.$http.get('/topics',{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(res){

vm.lists=res.data.data
})
})
},
beforeRouteUpdate:function(to,from,next){
var that=this
this.$http.get('/topics',{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(res){

that.lists=res.data.data
})
next();
}
}



